.popup__input {
  width: 100%;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding: 2px 0px 6px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}

.popup__input:last-of-type {
  margin-bottom: 20px;
}

.popup__input:focus {
  outline:none;
  border-bottom: 1px solid rgba(0, 0, 0, 1);
}